import React, { useState, useEffect } from 'react';
import {
  Grid,
  Box,
  Card,
  CardContent,
  CardHeader,
  Hidden
} from '@material-ui/core';
import Controls from 'src/components/controls/Controls';
import { useForm, Form } from 'src/components/useForm';
import { makeStyles } from '@material-ui/core/styles';

const useCardStyles = makeStyles({
  rightRoot: {
    padding: '1px',
    marginBottom: '5px'
  },
  leftRoot: {
    padding: '1px',
    marginBottom: '5px',
    marginRight: '5px'
  },
  root: {
    padding: '1px',
    marginBottom: '5px'
  }
});

export default function InvoiceTransactionForm(props) {
  const cardClasses = useCardStyles();

  const { addOrEdit, recordForEdit } = props;
  const { branchesSelectOptions } = props;
  const {
    branchParamsOptions,
    branchesOptions,
    initialBranchId,
    initialBranchParamOption,
    initialBranchCommunicationId,
    initialSupplierId,
    initialSupplierCommunicationId,
    initialTransporterId,
    initialTransporterCommunicationId
  } = branchesSelectOptions;

  console.log('branchParamsOptions: ' + JSON.stringify(branchParamsOptions));
  console.log('branchesOptions: ' + JSON.stringify(branchesOptions));

  const [invoiceStatusIdOptions, setInvoiceStatusIdOptions] = useState([
    { id: '1', title: 'Status 1' },
    { id: '2', title: 'Status 2' },
    { id: '3', title: 'Status 3' },
    { id: '4', title: 'Status 4' },
    { id: '5', title: 'Status 5' },
    { id: '6', title: 'Status 6' },
    { id: '7', title: 'Status 7' },
    { id: '8', title: 'Status 8' },
    { id: '9', title: 'Status 9' },
    { id: '10', title: 'Status 10' }
  ]);

  const [branchIdOptions, setBranchIdOptions] = useState(branchesOptions);
  const [
    branchCommunicationIdOptions,
    setBranchCommunicationIdOptions
  ] = useState(
    initialBranchParamOption ? initialBranchParamOption['communications'] : []
  );

  const [supplierIdOptions, setSupplierIdOptions] = useState(
    initialBranchParamOption ? initialBranchParamOption['suppliers'] : []
  );

  const [
    supplierCommunicationIdOptions,
    setSupplierCommunicationIdOptions
  ] = useState(
    initialBranchParamOption && initialSupplierId
      ? initialBranchParamOption['supplierCommunications'][initialSupplierId]
      : []
  );

  const [transporterIdOptions, setTransporterIdOptions] = useState(
    initialBranchParamOption ? initialBranchParamOption['transporters'] : []
  );

  const [
    transporterCommunicationIdOptions,
    setTransporterCommunicationIdOptions
  ] = useState(
    initialBranchParamOption && initialTransporterId
      ? initialBranchParamOption['transporterCommunications'][
          initialTransporterId
        ]
      : []
  );

  const [selectedBranchId, setSelectedBranchId] = useState(initialBranchId);

  const [
    selectedBranchCommunicationId,
    setSelectedBranchCommunicationId
  ] = useState(initialBranchCommunicationId);

  const [selectedSupplierId, setSelectedSupplierId] = useState(
    initialSupplierId
  );

  const [
    selectedSupplierCommunicationId,
    setSelectedSupplierCommunicationId
  ] = useState(initialSupplierCommunicationId);

  const [selectedTransporterId, setSelectedTransporterId] = useState(
    initialTransporterId
  );

  const [
    selectedTransporterCommunicationId,
    setSelectedTransporterCommunicationId
  ] = useState(initialTransporterCommunicationId);
  ////////////////////////////////////////

  const initialFValues = {
    branchId: selectedBranchId,
    branchCommunicationId: selectedBranchCommunicationId,
    invoiceReceivedDate: new Date(),
    invoiceDate: new Date(),
    invoiceNumber: '',
    invoiceStatusId: 1,
    lrdate: new Date(),
    lrnumber: '',
    numberOfParcels: 1,
    supplierId: selectedSupplierId,
    supplierCommunicationId: selectedSupplierCommunicationId,
    transporterId: selectedTransporterId,
    transporterCommunicationId: initialTransporterCommunicationId
  };

  const validate = (fieldValues = values) => {
    let temp = { ...errors };
    // if ('fullName' in fieldValues)
    //   temp.fullName = fieldValues.fullName ? '' : 'This field is required.';
    // if ('email' in fieldValues)
    //   temp.email = /$^|.+@.+..+/.test(fieldValues.email)
    //     ? ''
    //     : 'Email is not valid.';
    // if ('mobile' in fieldValues)
    //   temp.mobile =
    //     fieldValues.mobile.length > 9 ? '' : 'Minimum 10 numbers required.';
    // if ('departmentId' in fieldValues)
    //   temp.departmentId =
    //     fieldValues.departmentId.length != 0 ? '' : 'This field is required.';
    setErrors({
      ...temp
    });

    if (fieldValues == values) return Object.values(temp).every((x) => x == '');
  };

  const {
    values,
    setValues,
    errors,
    setErrors,
    handleInputChange,
    resetForm
  } = useForm(initialFValues, true, validate);

  /////////////////////////////////////////

  const handleBranchIdChange = (value) => {
    values.branchCommunicationId = '';
    values.supplierId = '';
    values.supplierCommunicationId = '';
    values.transporterId = '';
    values.transporterCommunicationId = '';

    values.branchId = value;

    if (value) {
      const currentBranchParamOption = branchParamsOptions[value];
      setBranchCommunicationIdOptions(
        currentBranchParamOption['communications']
      );
      setSupplierIdOptions(currentBranchParamOption['suppliers']);
      setTransporterIdOptions(currentBranchParamOption['transporters']);
      setSupplierCommunicationIdOptions([]);
      setTransporterCommunicationIdOptions([]);
    } else {
      setBranchCommunicationIdOptions([]);
      setSupplierIdOptions([]);
      setTransporterIdOptions([]);
      setSupplierCommunicationIdOptions([]);
      setTransporterCommunicationIdOptions([]);
    }
  };

  const handleBranchIdCommunicationIdChange = (value) => {
    values.branchCommunicationId = value;
  };

  const handleSupplierIdChange = (value) => {
    values.supplierCommunicationId = '';

    values.supplierId = value;

    if (value) {
      const currentBranchParamOption = branchParamsOptions[values.branchId];

      setSupplierCommunicationIdOptions(
        currentBranchParamOption['supplierCommunications'][value]
      );
    } else {
      setSupplierCommunicationIdOptions([]);
    }
  };

  const handleSupplierCommunicationIdChange = (value) => {
    values.supplierCommunicationId = value;
  };

  const handleTransporterIdChange = (value) => {
    values.transporterCommunicationId = '';

    values.transporterId = value;
    if (value) {
      const currentBranchParamOption = branchParamsOptions[values.branchId];
      setTransporterCommunicationIdOptions(
        currentBranchParamOption['transporterCommunications'][value]
      );
    } else {
      setTransporterCommunicationIdOptions([]);
    }
  };

  const handleTransporterCommunicationIdChange = (value) => {
    values.transporterCommunicationId = value;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      addOrEdit(values, resetForm);
    }
  };

  useEffect(() => {
    if (recordForEdit != null)
      setValues({
        ...recordForEdit
      });
  }, [recordForEdit]);

  const getBranchDetailsGrid = () => {
    return (
      <Grid item lg={12} md={12} xl={12} xs={12}>
        <Card className={cardClasses.root}>
          <CardHeader title="Branch Details" />
          <CardContent sx={{ padding: '0px !important' }}>
            <Grid container spacing={1}>
              <Grid item lg={6} md={6} xl={6} xs={6}>
                <Controls.Select
                  name="branchId"
                  label="Branch Code"
                  value={values.branchId}
                  options={branchIdOptions}
                  onChange={(e) => {
                    handleBranchIdChange(e.target.value);
                    handleInputChange(e);
                  }}
                  error={errors.branchId}
                  size="small"
                />
              </Grid>
              <Grid item lg={6} md={6} xl={6} xs={6}>
                <Controls.Select
                  name="branchCommunicationId"
                  label="Branch Communication Code"
                  value={values.branchCommunicationId}
                  options={branchCommunicationIdOptions}
                  onChange={(e) => {
                    handleBranchIdCommunicationIdChange(e.target.value);
                    handleInputChange(e);
                  }}
                  error={errors.branchCommunicationId}
                  size="small"
                />
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
    );
  };

  const toggleBranchDetailsView = () => {
    if (!branchIdOptions && !branchCommunicationIdOptions) return;
    // if (branchIdOptions.length <= 1 && branchCommunicationIdOptions.length <= 1)
    //   return <Hidden xsUp>{getBranchDetailsGrid()}</Hidden>;
    return getBranchDetailsGrid();
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Grid container>
        {toggleBranchDetailsView()}
        <Grid item lg={8} md={8} xl={8} xs={8}>
          <Card className={cardClasses.leftRoot}>
            <CardHeader title="Invoice Details" />
            <CardContent sx={{ padding: '0px !important' }}>
              <Grid container spacing={1}>
                <Grid item lg={6} md={6} xl={6} xs={6}>
                  <Controls.DatePicker
                    label="Invoice Received Date"
                    name="invoiceReceivedDate"
                    value={values.invoiceReceivedDate}
                    onChange={handleInputChange}
                    error={errors.invoiceReceivedDate}
                    size="small"
                  />
                </Grid>
                <Grid item lg={6} md={6} xl={6} xs={6}>
                  <Controls.DatePicker
                    label="Invoice Date"
                    name="invoiceDate"
                    value={values.invoiceDate}
                    onChange={handleInputChange}
                    error={errors.invoiceDate}
                    size="small"
                  />
                </Grid>
                <Grid item lg={6} md={6} xl={6} xs={6}>
                  <Controls.Input
                    label="Invoice Number"
                    name="invoiceNumber"
                    value={values.invoiceNumber}
                    onChange={handleInputChange}
                    error={errors.invoiceNumber}
                    size="small"
                  />
                </Grid>
                <Grid item lg={6} md={6} xl={6} xs={6}>
                  <Controls.Select
                    label="Invoice Status"
                    name="invoiceStatusId"
                    value={values.invoiceStatusId}
                    options={invoiceStatusIdOptions}
                    onChange={handleInputChange}
                    error={errors.invoiceStatusId}
                    size="small"
                  />
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
        <Grid item lg={4} md={4} xl={4} xs={4}>
          <Card className={cardClasses.rightRoot}>
            <CardHeader title="Supplier Details" />
            <CardContent sx={{ padding: '0px !important' }}>
              <Grid container spacing={1}>
                <Grid item lg={12} md={12} xl={12} xs={12}>
                  <Controls.Select
                    label="Supplier Code"
                    name="supplierId"
                    value={values.supplierId}
                    options={supplierIdOptions}
                    onChange={(e) => {
                      handleSupplierIdChange(e.target.value);
                      handleInputChange(e);
                    }}
                    error={errors.supplierId}
                    size="small"
                  />
                </Grid>
                <Grid item lg={12} md={12} xl={12} xs={12}>
                  <Controls.Select
                    label="Supplier Communication"
                    name="supplierCommunicationId"
                    value={values.supplierCommunicationId}
                    options={supplierCommunicationIdOptions}
                    onChange={(e) => {
                      handleSupplierCommunicationIdChange(e.target.value);
                      handleInputChange(e);
                    }}
                    error={errors.supplierCommunicationId}
                    size="small"
                  />
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>

        <Grid item lg={8} md={8} xl={8} xs={8}>
          <Card className={cardClasses.leftRoot}>
            <CardHeader title="LR Details" />
            <CardContent sx={{ padding: '0px !important' }}>
              <Grid container spacing={1}>
                <Grid item lg={6} md={6} xl={6} xs={6}>
                  <Controls.DatePicker
                    label="LR Date"
                    name="lrdate"
                    value={values.lrdate}
                    onChange={handleInputChange}
                    error={errors.lrdate}
                    size="small"
                  />
                </Grid>
                <Grid item lg={6} md={6} xl={6} xs={6}>
                  <Controls.Input
                    label="Number Of Parcels"
                    name="numberOfParcels"
                    value={values.numberOfParcels}
                    onChange={handleInputChange}
                    error={errors.numberOfParcels}
                    size="small"
                  />
                </Grid>
                <Grid item lg={6} md={6} xl={6} xs={6}>
                  <Controls.Input
                    label="LR Number"
                    name="lrnumber"
                    value={values.lrnumber}
                    onChange={handleInputChange}
                    error={errors.lrnumber}
                    size="small"
                  />
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
        <Grid item lg={4} md={4} xl={4} xs={4}>
          <Card className={cardClasses.rightRoot}>
            <CardHeader title="Transporter Details" />
            <CardContent sx={{ padding: '0px !important' }}>
              <Grid container spacing={1}>
                <Grid item lg={12} md={12} xl={12} xs={12}>
                  <Controls.Select
                    label="Transporter Code"
                    name="transporterId"
                    value={values.transporterId}
                    options={transporterIdOptions}
                    onChange={(e) => {
                      handleTransporterIdChange(e.target.value);
                      handleInputChange(e);
                    }}
                    error={errors.transporterId}
                    size="small"
                  />
                </Grid>
                <Grid item lg={12} md={12} xl={12} xs={12}>
                  <Controls.Select
                    label="Transporter Communication"
                    name="transporterCommunicationId"
                    value={values.transporterCommunicationId}
                    options={transporterCommunicationIdOptions}
                    onChange={(e) => {
                      handleTransporterCommunicationIdChange(e.target.value);
                      handleInputChange(e);
                    }}
                    error={errors.transporterCommunicationId}
                    size="small"
                  />
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-end',
          marginTop: '4px'
        }}
      >
        <Controls.Button
          type="submit"
          text="Submit"
          sx={{
            mr: 2
          }}
        />
        <Controls.Button text="Reset" color="default" onClick={resetForm} />
      </Box>
    </Form>
  );
}
