import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Box,
  Button,
  Container,
  TextField,
  Typography
} from '@material-ui/core';
import axios from 'axios';
import CircularProgress from '@material-ui/core/CircularProgress';

const Login = (props) => {
  const { setToken } = props;
  const { pathname } = useLocation();
  console.log('Login ==> pathname: ' + pathname);

  useEffect(() => {
    console.log('Loggin.useEffect');
  }, []);

  return (
    <>
      <Helmet>
        <title>Login | IP Tracking</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.default',
          display: 'flex',
          flexDirection: 'column',
          height: '100%',
          justifyContent: 'center'
        }}
      >
        <Container maxWidth="sm">
          <Formik
            initialValues={{
              userName: '',
              password: '',
              clientCode: ''
            }}
            validationSchema={Yup.object().shape({
              userName: Yup.string()
                .max(32, 'Must be 32 characters or less')
                .required('User Name is required'),
              clientCode: Yup.string()
                .max(64, 'Must be 64 characters or less')
                .required('Client Code is required'),
              password: Yup.string()
                .max(128, 'Must be 128 characters or less')
                .required('Password is required')
            })}
            onSubmit={(values, { setSubmitting, setFieldError }) => {
              const data = {
                ClientCode: values.clientCode,
                Username: values.userName,
                Password: values.password
              };
              axios
                .post('api/Account/Authenticate', data)
                .then((res) => {
                  const token = res.data;
                  console.log('API_RESPONSE: ' + JSON.stringify(token));
                  setSubmitting(false);
                  if (setToken) {
                    console.log('setToken in Login is present');
                    setToken(token);
                  }
                })
                .catch((err) => {
                  setSubmitting(false);
                  console.log('error', err);
                  const errorMessage = err.response?.data?.message;
                  console.log('errorMessage: ', errorMessage);
                  setFieldError('general', errorMessage);
                });
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              handleReset,
              isSubmitting,
              touched,
              values
            }) => (
              <form onSubmit={handleSubmit} onReset={handleReset}>
                <Box sx={{ mb: 3 }}>
                  <Typography color="textPrimary" variant="h2">
                    Sign in
                  </Typography>
                </Box>
                <TextField
                  error={Boolean(touched.userName && errors.userName)}
                  fullWidth
                  helperText={touched.userName && errors.userName}
                  label="User Name"
                  margin="normal"
                  name="userName"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="text"
                  value={values.userName}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.clientCode && errors.clientCode)}
                  fullWidth
                  helperText={touched.clientCode && errors.clientCode}
                  label="Client Code"
                  margin="normal"
                  name="clientCode"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="text"
                  value={values.clientCode}
                  variant="outlined"
                />
                <TextField
                  error={Boolean(touched.password && errors.password)}
                  fullWidth
                  helperText={touched.password && errors.password}
                  label="Password"
                  margin="normal"
                  name="password"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="password"
                  value={values.password}
                  variant="outlined"
                />
                {isSubmitting ? (
                  <Box
                    sx={{ py: 2, display: 'flex', justifyContent: 'center' }}
                  >
                    <CircularProgress />
                  </Box>
                ) : (
                  <React.Fragment>
                    <Box sx={{ pt: 2 }}>
                      <Typography color="red" variant="h6">
                        {errors.general}
                      </Typography>
                    </Box>
                    <Box sx={{ py: 2 }}>
                      <Button
                        color="primary"
                        disabled={isSubmitting}
                        size="large"
                        type="submit"
                        variant="contained"
                        sx={{ mr: 2 }}
                      >
                        Sign in now
                      </Button>
                      <Button
                        color="secondary"
                        size="large"
                        variant="contained"
                        type="reset"
                      >
                        Reset
                      </Button>
                    </Box>
                  </React.Fragment>
                )}
              </form>
            )}
          </Formik>
        </Container>
      </Box>
    </>
  );
};

export default Login;
