import React, { useState } from 'react';
import { Navigate } from 'react-router-dom';
import DashboardLayout from 'src/components/DashboardLayout';
import Dashboard from 'src/pages/Dashboard';
import Login from 'src/pages/Login';
import NotFound from 'src/pages/NotFound';
import InvoiceTransactions from 'src/pages/InvoiceTransactions';
import Branch from 'src/pages/Branch';
import Site from 'src/pages/Site';
import Transporter from 'src/pages/Transporter';
import Supplier from 'src/pages/Supplier';
import Logout from 'src/pages/Logout';

const AppRoutes = (token, setToken, company, setCompany) => {
  console.log('AppRoutes ==> token: ' + token);
  return [
    {
      path: '/',
      element: (
        <DashboardLayout
          token={token}
          setToken={setToken}
          company={company}
          setCompany={setCompany}
        />
      ),
      children: [
        {
          path: 'dashboard',
          element: token ? (
            <Dashboard
              token={token}
              setToken={setToken}
              company={company}
              setCompany={setCompany}
            />
          ) : (
            <Navigate to="/login" />
          )
        },
        {
          path: 'invoices',
          element: token ? (
            <InvoiceTransactions
              token={token}
              setToken={setToken}
              company={company}
              setCompany={setCompany}
            />
          ) : (
            <Navigate to="/login" />
          )
        },
        {
          path: 'branch',
          element: token ? (
            <Branch
              token={token}
              setToken={setToken}
              company={company}
              setCompany={setCompany}
            />
          ) : (
            <Navigate to="/login" />
          )
        },
        {
          path: 'site',
          element: token ? (
            <Site
              token={token}
              setToken={setToken}
              company={company}
              setCompany={setCompany}
            />
          ) : (
            <Navigate to="/login" />
          )
        },
        {
          path: 'transporter',
          element: token ? (
            <Transporter
              token={token}
              setToken={setToken}
              company={company}
              setCompany={setCompany}
            />
          ) : (
            <Navigate to="/login" />
          )
        },
        {
          path: 'supplier',
          element: token ? (
            <Supplier
              token={token}
              setToken={setToken}
              company={company}
              setCompany={setCompany}
            />
          ) : (
            <Navigate to="/login" />
          )
        },
        {
          path: 'login',
          element: token ? (
            <Navigate to="/dashboard" />
          ) : (
            <Login setToken={setToken} setCompany={setCompany} />
          )
        },
        {
          path: 'logout',
          element: token ? (
            <Logout setToken={setToken} setCompany={setCompany} />
          ) : (
            <Navigate to="/login" />
          )
        },
        {
          path: '*',
          element: token ? (
            <NotFound
              token={token}
              setToken={setToken}
              company={company}
              setCompany={setCompany}
            />
          ) : (
            <NotFound />
          )
        }
      ]
    }
  ];
};

export default AppRoutes;
