import React, { useState } from 'react';
import InvoiceTransactionForm from 'src/pages/InvoiceTransactionForm';
import {
  Paper,
  makeStyles,
  TableBody,
  TableRow,
  TableCell,
  Toolbar,
  InputAdornment
} from '@material-ui/core';
import useTable from 'src/components/useTable';
import Controls from 'src/components/controls/Controls';
import { Search } from '@material-ui/icons';
import AddIcon from '@material-ui/icons/Add';
import Popup from 'src/components/Popup';
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import CloseIcon from '@material-ui/icons/Close';
import { Helmet } from 'react-helmet';
import { Box, Card, CardHeader, Container, Grid } from '@material-ui/core';
import PerfectScrollbar from 'react-perfect-scrollbar';
import moment from 'moment';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  pageContent: {
    margin: theme.spacing(5),
    padding: theme.spacing(3)
  },
  searchInput: {
    width: '85%'
  }
}));

const headCells = [
  { id: 'invoiceNumber', label: 'Invoice Number' },
  { id: 'invoiceReceivedDate', label: 'Invoice Recieved Date' },
  { id: 'lrnumber', label: 'LR Number' },
  { id: 'lrdate', label: 'LR Date' },
  { id: 'numberOfParcels', label: 'Parcel Count' },
  { id: 'actions', label: 'Actions', disableSorting: true }
];

export default function InvoiceTransactions(props) {
  const classes = useStyles();
  const { token, setToken, company, setCompany } = props;
  const [recordForEdit, setRecordForEdit] = useState(null);
  const [popupActionType, setPopupActionType] = useState('add');
  const [records, setRecords] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filterFn, setFilterFn] = useState({
    fn: (items) => {
      return items;
    }
  });
  const [openPopup, setOpenPopup] = useState(false);
  const branchesSelectOptions = transformBranches(company);

  const {
    TblContainer,
    TblHead,
    TblPagination,
    recordsAfterPagingAndSorting
  } = useTable(records, headCells, filterFn);

  const handleSearch = (e) => {
    let target = e.target;
    setFilterFn({
      fn: (items) => {
        if (target.value == '' || !target.value) return items;
        else
          return items.filter(
            (x) =>
              x.invoiceNumber
                .toLowerCase()
                .includes(target.value.toLowerCase()) ||
              x.lrnumber.toLowerCase().includes(target.value.toLowerCase())
          );
      }
    });
  };

  const addOrEdit = (employee, resetForm) => {
    // if (employee.id == 0) employeeService.insertEmployee(employee);
    // else employeeService.updateEmployee(employee);
    // resetForm();
    // setRecordForEdit(null);
    // setOpenPopup(false);
    // setRecords(employeeService.getAllEmployees());
    //add api call
  };

  const openInPopup = (actionType, item) => {
    setPopupActionType(actionType);
    setRecordForEdit(item);
    setOpenPopup(true);
  };

  const fetchInvoices = () => {
    axios
      .get(
        `api/Transaction/GetAllInvoicesByCompany/?cmpID=${company.companyId}`,
        {
          headers: { Authorization: `Bearer ${token.jwtToken}` }
        }
      )
      .then((res) => {
        setLoading(false);
        console.log(JSON.stringify(res.data));
        setRecords(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  React.useEffect(() => {
    setLoading(true);
    fetchInvoices();
  }, [company]);

  return (
    <>
      <Helmet>
        <title>Invoice Inward</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.default',
          minHeight: '100%',
          py: 3
        }}
      >
        <Container maxWidth={false}>
          <Grid container spacing={3}>
            <Grid item lg={12} md={12} xl={12} xs={12}>
              <Card>
                <CardHeader title="" />
                <PerfectScrollbar>
                  <Paper className={classes.pageContent}>
                    <Toolbar className={classes.seachToolbar}>
                      <Box display="flex" flexGrow={1}>
                        <Controls.Input
                          label="Search"
                          className={classes.searchInput}
                          InputProps={{
                            startAdornment: (
                              <InputAdornment position="start">
                                <Search />
                              </InputAdornment>
                            )
                          }}
                          onChange={handleSearch}
                        />
                      </Box>
                      <Controls.Button
                        text=""
                        variant="outlined"
                        startIcon={<AddIcon />}
                        onClick={() => {
                          openInPopup('add', null);
                        }}
                      />
                    </Toolbar>
                    <TblContainer>
                      <TblHead />
                      <TableBody>
                        {recordsAfterPagingAndSorting().map((item) => (
                          <TableRow key={item.id}>
                            <TableCell>{item.invoiceNumber}</TableCell>
                            <TableCell>
                              {moment(item.invoiceReceivedDate).format(
                                'DD/MM/YYYY'
                              )}
                            </TableCell>
                            <TableCell>{item.lrnumber}</TableCell>
                            <TableCell>
                              {moment(item.lrdate).format('DD/MM/YYYY')}
                            </TableCell>
                            <TableCell>{item.numberOfParcels}</TableCell>
                            <TableCell>
                              <Controls.ActionButton
                                color="primary"
                                onClick={() => {
                                  openInPopup('edit', item);
                                }}
                              >
                                <EditOutlinedIcon fontSize="small" />
                              </Controls.ActionButton>
                              <Controls.ActionButton color="secondary">
                                <CloseIcon fontSize="small" />
                              </Controls.ActionButton>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </TblContainer>
                    <TblPagination />
                  </Paper>
                  <Popup
                    title={
                      popupActionType === 'edit'
                        ? 'Edit Invoice'
                        : 'Add Invoice'
                    }
                    openPopup={openPopup}
                    setOpenPopup={setOpenPopup}
                  >
                    <InvoiceTransactionForm
                      recordForEdit={recordForEdit}
                      addOrEdit={addOrEdit}
                      token={token}
                      setToken={setToken}
                      company={company}
                      setCompany={setCompany}
                      branchesSelectOptions={branchesSelectOptions}
                    />
                  </Popup>
                </PerfectScrollbar>
              </Card>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
}

function transformCommunications(branch) {
  const communicationOptions = [];

  const communications = branch.branchCommunication;
  if (!communications) return communicationOptions;
  for (let i = 0; i < communications.length; i++) {
    const branchCommunication = communications[i];
    const address = branchCommunication.communication?.address;

    const title = address?.addressLine1
      ? address?.addressLine1 + ', ' + address?.addressLine2
      : address?.addressLine2;

    communicationOptions.push({
      id: '' + branchCommunication.id,
      title: title
    });
  }

  return communicationOptions;
}

function transformSites(branch) {
  const sitesOptions = [];
  const sitesCommunicationOptions = {};

  const sites = branch.branchSite;
  if (!sites) return sitesOptions;

  for (let i = 0; i < sites.length; i++) {
    const site = sites[i]?.site;
    if (site) {
      sitesOptions.push({ id: '' + site.id, title: site.code });

      const siteCommunicationOptions = [];
      const siteCommunications = site.siteCommunication;
      if (siteCommunications) {
        for (let j = 0; j < siteCommunications.length; j++) {
          const siteCommunication = siteCommunications[j];
          if (siteCommunication) {
            const address = siteCommunication.communication.address;
            const title = address?.addressLine1
              ? address?.addressLine1 + ', ' + address?.addressLine2
              : address?.addressLine2;

            siteCommunicationOptions.push({
              id: '' + siteCommunication.id,
              title: title
            });
          }
        }
      }

      sitesCommunicationOptions[site.id] = siteCommunicationOptions;
    }
  }
  return { sitesOptions, sitesCommunicationOptions };
}

function transformSuppliers(branch) {
  const suppliersOptions = [];
  const suppliersCommunicationOptions = {};

  const suppliers = branch.branchSupplier;
  if (!suppliers) return suppliersOptions;

  for (let i = 0; i < suppliers.length; i++) {
    const supplier = suppliers[i]?.supplier;
    if (supplier) {
      suppliersOptions.push({ id: '' + supplier.id, title: supplier.code });

      const supplierCommunicationOptions = [];
      const supplierCommunications = supplier.supplierCommunication;
      if (supplierCommunications) {
        for (let j = 0; j < supplierCommunications.length; j++) {
          const supplierCommunication = supplierCommunications[j];
          if (supplierCommunication) {
            const address = supplierCommunication.communication.address;
            const title = address?.addressLine1
              ? address?.addressLine1 + ', ' + address?.addressLine2
              : address?.addressLine2;

            supplierCommunicationOptions.push({
              id: '' + supplierCommunication.id,
              title: title
            });
          }
        }
      }

      suppliersCommunicationOptions[supplier.id] = supplierCommunicationOptions;
    }
  }
  return { suppliersOptions, suppliersCommunicationOptions };
}

function transformTransporters(branch) {
  const transportersOptions = [];
  const transportersCommunicationOptions = {};

  const transporters = branch.branchTransporter;
  if (!transporters) return transportersOptions;

  for (let i = 0; i < transporters.length; i++) {
    const transporter = transporters[i]?.transporter;
    if (transporter) {
      transportersOptions.push({
        id: '' + transporter.id,
        title: transporter.code
      });

      const transporterCommunicationOptions = [];
      const transporterCommunications = transporter.transporterCommunication;
      if (transporterCommunications) {
        for (let j = 0; j < transporterCommunications.length; j++) {
          const transporterCommunication = transporterCommunications[j];
          if (transporterCommunication) {
            const address = transporterCommunication.communication.address;
            const title = address?.addressLine1
              ? address?.addressLine1 + ', ' + address?.addressLine2
              : address?.addressLine2;

            transporterCommunicationOptions.push({
              id: '' + transporterCommunication.id,
              title: title
            });
          }
        }
      }

      transportersCommunicationOptions[
        transporter.id
      ] = transporterCommunicationOptions;
    }
  }
  return { transportersOptions, transportersCommunicationOptions };
}

function transformBranches(company) {
  const branchParamsOptions = {};
  const branchesOptions = [];

  for (let i = 0; i < company.branches.length; i++) {
    const branch = company.branches[i];

    if (branch) {
      const branchId = branch.branchId;
      const branchFilterOption = {
        id: '' + branchId,
        title: branch.branchCode
      };

      branchesOptions.push(branchFilterOption);

      const branchOptions = {};
      branchOptions['communications'] = transformCommunications(branch);

      const { sitesOptions, sitesCommunicationOptions } = transformSites(
        branch
      );
      branchOptions['sites'] = sitesOptions;
      branchOptions['siteCommunications'] = sitesCommunicationOptions;

      const {
        suppliersOptions,
        suppliersCommunicationOptions
      } = transformSuppliers(branch);
      branchOptions['suppliers'] = suppliersOptions;
      branchOptions['supplierCommunications'] = suppliersCommunicationOptions;

      const {
        transportersOptions,
        transportersCommunicationOptions
      } = transformTransporters(branch);
      branchOptions['transporters'] = transportersOptions;
      branchOptions[
        'transporterCommunications'
      ] = transportersCommunicationOptions;

      branchParamsOptions[branchId] = branchOptions;
    }
  }

  let initialBranchId = '';
  let initialBranchCommunicationId = '';
  let initialBranchParamOption;
  if (branchesOptions.length === 1) {
    initialBranchId = branchesOptions[0].id;

    initialBranchParamOption = branchParamsOptions[initialBranchId];

    const branchCommunicationOptions =
      initialBranchParamOption['communications'];
    if (branchCommunicationOptions.length === 1) {
      initialBranchCommunicationId = branchCommunicationOptions[0].id;
    }
  }

  const initialSupplierId = '';
  const initialSupplierCommunicationId = '';
  const initialTransporterId = '';
  const initialTransporterCommunicationId = '';

  return {
    branchParamsOptions: branchParamsOptions,
    branchesOptions: branchesOptions,
    initialBranchId,
    initialBranchParamOption,
    initialBranchCommunicationId,
    initialSupplierId,
    initialSupplierCommunicationId,
    initialTransporterId,
    initialTransporterCommunicationId
  };
}
