import React, { Fragment, useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
  AppBar,
  Badge,
  Box,
  IconButton,
  Toolbar,
  makeStyles
} from '@material-ui/core';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Logout from '@material-ui/icons/Logout';
import axios from 'axios';
import Logo from './Logo';

const useStyles = makeStyles((theme) => ({
  navLink: {
    color: 'white !important'
  }
}));

const DashboardNavbar = (props) => {
  const { onMobileNavOpen, token, company, setCompany, ...rest } = props;
  const classes = useStyles();
  const [notifications] = useState([]);
  const [anchorEl, setAnchorEl] = useState(null);

  const selectedIndex = company ? company.index : 0;

  const options = token?.userCompanyUser.map((item) => item.companyCode);

  const fetchCompanyDetails = (selectedCompany) => {
    axios
      .get(
        `api/Assignment/GetBranchTSSByUser?userId=${token.id}&cmpID=${selectedCompany.companyId}`,
        {
          headers: { Authorization: `Bearer ${token.jwtToken}` }
        }
      )
      .then((res) => {
        console.log(JSON.stringify(res.data));
        selectedCompany['branches'] = res.data;
        setCompany(selectedCompany);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const bindCompany = (index) => {
    let selectedCompany;
    if (!company || company.index !== index) {
      selectedCompany = {
        ...token.userCompanyUser[index]
      };
      fetchCompanyDetails(selectedCompany);
      selectedCompany['index'] = index;
    } else {
      selectedCompany = company;
    }
  };

  useEffect(() => {
    if (token) bindCompany(selectedIndex);
  }, [token, company]);

  const handleClickListItem = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuItemClick = (event, index) => {
    bindCompany(index);
    setAnchorEl(null);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar elevation={0} {...rest}>
      <Toolbar>
        <RouterLink to="/">{<Logo />}</RouterLink>
        <Box sx={{ flexGrow: 1 }} />
        {token ? (
          <Fragment>
            <List component="nav" aria-label="Device settings">
              <ListItem
                button
                aria-haspopup="true"
                aria-controls="lock-menu"
                aria-label="when device is locked"
                onClick={handleClickListItem}
              >
                <ListItemText primary={options[selectedIndex]} />
              </ListItem>
            </List>
            <Menu
              id="lock-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              {options.map((option, index) => (
                <MenuItem
                  key={option}
                  selected={index === selectedIndex}
                  onClick={(event) => handleMenuItemClick(event, index)}
                >
                  {option}
                </MenuItem>
              ))}
            </Menu>
            <IconButton color="inherit">
              <Badge
                badgeContent={notifications.length}
                color="primary"
                variant="dot"
              >
                <RouterLink to="/logout" className={classes.navLink}>
                  <Logout />
                </RouterLink>
              </Badge>
            </IconButton>
          </Fragment>
        ) : (
          <Fragment></Fragment>
        )}
      </Toolbar>
    </AppBar>
  );
};

DashboardNavbar.propTypes = {
  onMobileNavOpen: PropTypes.func
};

export default DashboardNavbar;
