import { Children, useEffect, Fragment } from 'react';
import { useLocation } from 'react-router-dom';
import PropTypes from 'prop-types';
import { Box, Drawer, Hidden, List } from '@material-ui/core';
import {
  Home as HomeIcon,
  BarChart as BarChartIcon,
  User as UserIcon,
  Package as PackageIcon,
  FileText as FileTextIcon,
  Clipboard as ClipboarIcon,
  Tag as TagIcon
} from 'react-feather';
import NavItem from './NavItem';

const items = [
  {
    href: '/dashboard',
    icon: HomeIcon,
    title: 'Dashboard'
  },
  {
    icon: BarChartIcon,
    title: 'Transaction',
    children: [
      {
        href: '/invoices',
        icon: FileTextIcon,
        title: 'Invoice Inward'
      },
      {
        href: '/parcels',
        icon: PackageIcon,
        title: 'Parcel Inward'
      }
    ]
  },
  {
    href: '/assignment',
    icon: ClipboarIcon,
    title: 'Assignment'
  },
  {
    icon: TagIcon,
    title: 'Definition',
    children: [
      {
        href: '/branch',
        icon: FileTextIcon,
        title: 'Branch'
      },
      {
        href: '/site',
        icon: PackageIcon,
        title: 'Site'
      },
      {
        href: '/transporter',
        icon: PackageIcon,
        title: 'Transporter'
      },
      {
        href: '/supplier',
        icon: PackageIcon,
        title: 'Supplier'
      }
    ]
  },
  {
    href: '/admin',
    icon: UserIcon,
    title: 'Admin'
  }
];

const DashboardSidebar = ({ onMobileClose, openMobile, token, ...rest }) => {
  const location = useLocation();

  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
  }, [location.pathname]);

  const content = (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%'
      }}
    >
      <Box sx={{ p: 2 }}>
        <List>
          {items.map((item) => (
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
              children={item.children}
            />
          ))}
        </List>
      </Box>
      <Box sx={{ flexGrow: 1 }} />
    </Box>
  );

  return (
    <Fragment>
      {token ? (
        <Fragment>
          <Hidden lgUp>
            <Drawer
              anchor="left"
              onClose={onMobileClose}
              open={openMobile}
              variant="temporary"
              PaperProps={{
                sx: {
                  width: 256
                }
              }}
            >
              {content}
            </Drawer>
          </Hidden>
          <Hidden lgDown>
            <Drawer
              anchor="left"
              open
              variant="persistent"
              PaperProps={{
                sx: {
                  width: 256,
                  top: 64,
                  height: 'calc(100% - 64px)'
                }
              }}
            >
              {content}
            </Drawer>
          </Hidden>
        </Fragment>
      ) : (
        <Fragment> </Fragment>
      )}
    </Fragment>
  );
};

DashboardSidebar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

DashboardSidebar.defaultProps = {
  onMobileClose: () => {},
  openMobile: false
};

export default DashboardSidebar;
